<template>
  <div class="card">
    <div class="card-header">Exam07View</div>
    <div class="card-body">
      <div class="card">
        <div class="card-header">Parameter로 전달</div>
        <div class="card-body">
          <ul>
            <li><router-link to="/menu01/exam08view/freeboard/blue">exam08view/freeboard/blue</router-link></li>
            <li><router-link v-bind:to="`/menu01/exam08view/${kind1}/${color1}`">exam08view/{{kind1}}/{{color1}}</router-link></li>
            <li><router-link :to="{path: `/menu01/exam08view/${kind2}/${color2}`}">exam08view/{{kind2}}/{{color2}}</router-link></li>
            <li><router-link :to="{name: 'menu01_exam08view', params: {kind: kind3, color: color3}}">exam08view/{{kind3}}/{{color3}}</router-link></li>
            <li>
              <button @click="goUrlWithParams()" class="btn btn-info btn-sm mr-2">exam08view/{{kind3}}/{{color3}}</button>
              <a href="#" @click="goUrlWithParams()" class="btn btn-info btn-sm">exam08view/{{kind3}}/{{color3}}</a>
            </li>
          </ul>
        </div>
      </div>

      <div class="card">
        <div class="card-header">Query String으로 전달</div>
        <div class="card-body">
          <ul>
            <li><router-link to="/menu01/exam08view?kind=freeboard&color=blue">exam08view?kind=freeboard&color=blue</router-link></li>
            <li><router-link v-bind:to="`/menu01/exam08view?kind=${kind1}&color=${color1}`">exam08view?kind={{kind1}}&color={{color1}}</router-link></li>
            <li><router-link :to="{path: `/menu01/exam08view?kind=${kind2}&color=${color2}`}">exam08view?kind={{kind2}}&color={{color2}}</router-link></li>
            <li><router-link :to="{name: 'menu01_exam08view', query: {kind: kind3, color: color3}}">exam08view?kind={{kind3}}&color={{color3}}</router-link></li>
            <li>
              <button @click="goUrlWithQueryString()" class="btn btn-info btn-sm mr-2">exam08view/{{kind3}}/{{color3}}</button>
              <a href="#" @click="goUrlWithQueryString()" class="btn btn-info btn-sm">exam08view/{{kind3}}/{{color3}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useRouter } from "vue-router";
const router = useRouter();

const kind1 = "notice";
const kind2 = "qa";
const kind3 = "album";
const color1 = "red";
const color2 = "green";
const color3 = "yellow";

function goUrlWithParams() {
  router.push({
    name: "menu01_exam08view",
    params: {kind: kind3, color: color3}
  });
}

function goUrlWithQueryString() {
  router.push({
    name: "menu01_exam08view",
    query: {kind: kind3, color: color3}
  });
}
</script>

<style scoped>
</style>



